<template>
    <h1>Get an Email</h1>
    <input type="email" placeholder="From:" v-model="email" required/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getMail">Confirm</button>
</template>

<script>

var validator = require("email-validator");

export default {
    name: "MailA",
    data() {
        return {
            email: null
        }
    },
    methods: {
        getMail() {
            if (validator.validate(this.email)) {
                let A = {
                    "object": this.email
                }
                let Action = {
                    "content": JSON.stringify(A),
                    "id": "633856703a79d71704729438"
                }
                localStorage.action = JSON.stringify(Action);
                this.$router.push({ name: 'Recap' });
            } else {
                alert('Email is not valid')
            }
        },
    }
}
</script>

